/* FOOTER */

.main-footer {
  @media (min-width: @desktop-width) {
    border-radius: 60px 60px 0 0;
    background-color: @base-light;
  }
}

.main-footer__main {
  display: grid;
  background-color: @base-light;
  border-radius: 30px 30px 0 0;
  text-align: center;
  padding: 37px 25px 19px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 24% 52% 24%;
    align-items: center;
    padding: 46px 40px 32px;
    border-radius: 40px 40px 0 0;
  }

  @media (min-width: @desktop-width) {
    padding: 47px 60px 43px;
    width: 1440px;
    margin: 0 auto;
    box-sizing: border-box;
  }
}

.main-footer__logo {
  margin: 0 0 40px;
  justify-self: center;

  @media (min-width: @tablet-width) {
    position: relative;
    justify-self: end;
    // margin-right: 10px;
    margin: 0;
    width: 107px;
    height: 17px;
  }

  @media (min-width: @desktop-width) {
    justify-self: start;
    margin-left: 71px;
  }
}

.main-footer__logo-icon {
  @media (min-width: @tablet-width) {
    width: 107px;
    height: 17px;
  }
}

.main-footer__main::before {
  @media (min-width: @tablet-width) {
    position: absolute;
    z-index: 0;
    content: "";
    width: 155px;
    height: 42px;
    background: url("../images/color-logo-tablet.svg");
  }
}

