.main-footer__logo-link {
  margin: 0;
  font-family: "Blogger Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 16px;
  letter-spacing: 0;
  text-align: left;
  color: @base-grey;
  text-decoration: none;
  padding-top: 13px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 270px;
  min-height: 33px;
  border-top: 1px solid @footer-border-color;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 20px;
    margin-right: 23px;
    border: 0;
    padding: 0;
    justify-content: flex-end;
    width: auto;
  }
}

.main-footer__logo-left {
  margin-left: 24px;
}
